
.screen {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}
.screen__content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 50%;
    margin-left: auto;
    height: 100%;
    padding: 50px 30px;
    background-color: rgba(0, 128, 0, 0.7);
    max-width: 650px;
    -webkit-backdrop-filter: blur(7px);
            backdrop-filter: blur(7px);
}
@media (max-width: 768px) {
.screen__content {
        min-width: 450px;
}
}
@media (max-width: 480px) {
.screen__content {
        width: 100%;
        min-width: 0;
}
}
.screen__title {
    margin-left: -30px;
}
.screen .slide-enter-active, .screen .slide-leave-active {
    transition: .3s;
}
.screen .slide-enter {
    opacity: 0;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.screen .slide-fade-enter-active, .screen .slide-fade-leave-active {
    transition: 1s;
}
.screen .slide-fade-enter, .screen .slide-fade-leave {
    opacity: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}



